import React from "react";
import { useDispatch } from "react-redux";
import { Box, Paper, Button } from "@material-ui/core";
import { cartUpdateItemAction } from "../../actions/Cart";

export default function ProdutoFilho(props) {
  const produto = props.dados;
  const atributos = produto.attributes;
  const dispatch = useDispatch();

  let genero = atributos.find(obj => obj.name === "Gênero");
  genero = genero ? genero.options : [];

  let dias = atributos.find(obj => obj.name === "Dias");
  dias = dias ? dias.options : [];

  function addItem() {
    dispatch(cartUpdateItemAction(produto, 1));
  }

  return (
    <Box display="flex" flexDirection="column" justifyContent="center" p={1}>
      <Box>{dias}</Box>
      <Box>{genero}</Box>
      <Paper p={1}>
        <Box>{produto.price}</Box>
        <Button size="small" variant="contained" onClick={addItem}>
          Comprar
        </Button>
      </Paper>
    </Box>
  );
}
